import React from 'react';
import { IconFontColor } from '../icons';

const GRADIENT_FLAG = '__';

function isGradientColor(color: string) {
  // 判断是否为渐变色，渐变色格式为 ‘#color1__#color2’
  return color && color.includes(GRADIENT_FLAG);
};

// 将 #color1_#color2 转换为 [color1, color2]
function transferGradientColor(color: string) {
  if (!isGradientColor(color)) {
    return [];
  }
  return color.split(GRADIENT_FLAG);
}


interface IFontIcon {
  color: string;
}

const FontColorIcon: React.FC<IFontIcon> = (props) => {
  const { color } = props;

  return isGradientColor(color) ? (
    <GradientColorFontIcon color={color} />
  ) : (
    <IconFontColor subColor={color}/>
  );
};

const GradientColorFontIcon: React.FC<IFontIcon> = (props) => {
  const { color } = props;
  const [startColor, endColor] = transferGradientColor(color);
  return (
    <svg
      width="20px"
      height="20px"
      fill="currentColor"
      aria-hidden="true"
      focusable="false"
      viewBox="0 0 20 20"
    >
      <path
        d="M19 20H1V17H19V20Z"
        fill={`url(#paint0_linear_${startColor}:${endColor})`}
      ></path>
      <path
        d="M7.53848 11L5.93848 15H4.86145L9.53573 3.3143H10.4642L15.1385 15H14.0615L12.4615 11H7.53848ZM7.93848 10H12.0615L9.99997 4.84629L7.93848 10Z"
        fill="inherit"
      />
      <defs>
        <linearGradient
          id={`paint0_linear_${startColor}:${endColor}`}
          x1="4"
          y1="9.5"
          x2="16"
          y2="9.5"
          gradientUnits="userSpaceOnUse"
        >
          <stop stopColor={startColor} />
          <stop offset="1" stopColor={endColor} />
        </linearGradient>
      </defs>
    </svg>
  );
};

export default FontColorIcon;
